import React, { useEffect, useState } from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
} from "recharts";

export default function Dashboard() {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch("http://127.0.0.1:8000/api/registros/")
      .then((res) => res.json())
      .then((res) => {
        // Convertimos los datos a un formato adecuado para la grfica
        const processed = res.map((item) => ({
          nombre: item.nombre || "Sin nombre",
          edad: item.edad || 0,
          ciudad: item.ciudad || "Sin ciudad",
        }));
        setData(processed);
      });
  }, []);

  return (
    <div className="p-6">
      <h1 className="text-xl font-bold mb-4">Dashboard de Registros</h1>
      <BarChart width={700} height={400} data={data}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="nombre" />
        <YAxis />
        <Tooltip />
        <Bar dataKey="edad" fill="#82ca9d" />
      </BarChart>
    </div>
  );
}
